<template>
  <v-container class="fill-height fill-width pa-0">
    <v-row class="text-left" align="center" justify="center">
      <div class="bodyContent">
        <!-- Missing Data when fill the form -->
        <v-dialog v-model="dataMissingModal" persistent max-width="350">
          <v-card class="pt-3" align="center">
            <v-icon align="center" color="red darken-3" size="80">
              fa-exclamation-triangle
            </v-icon>
            <p class="text_title_alert">ขออภัย พบข้อผิดพลาด</p>
            <v-card-text align="left"
              >ข้อมูลผู้ใช้ขาดหายระหว่างดำเนินการกรอกข้อมูล
              กลับสู่หน้าผู้ใช้งานหลักและเลือกแก้ไขรายงาน</v-card-text
            >
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn rounded large text color="red darken-1" @click="exitReport"
                >รับทราบ</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>

        <!-- Confirm Case (final step to sending the case, user cann't change or edit later) -->
        <v-dialog v-model="caseSendingModal" persistent max-width="350">
          <v-card align="center" :loading="loadingCaseSending">
            <v-icon
              class="mt-8"
              align="center"
              color="light-blue lighten-2"
              size="80"
            >
              fa-file-alt
            </v-icon>
            <p class="text_title_question">ยืนยันการส่งรายงาน</p>
            <v-card-text align="left"
              >หากท่านยืนยันการส่งรายงาน
              รายงานฉบับดังกล่าวจะไม่สามารถแก้ไขได้อีก
              และระบบจะทำการส่งมอบให้เจ้าที่รับผิดชอบ</v-card-text
            >
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                rounded
                large
                text
                color="grey lighten-1"
                @click="caseSendingModal = false"
                :disabled="loadingCaseSending"
                >ยกเลิก</v-btn
              >
              <v-btn
                rounded
                large
                text
                color="light-blue darken-1"
                @click="sendingCase"
                :disabled="loadingCaseSending"
                >ยืนยัน</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>

        <!-- Result of sending case report to server: Success condition -->
        <v-dialog v-model="caseResultModal" persistent max-width="350">
          <v-card align="center">
            <v-icon
              class="mt-8"
              align="center"
              color="green lighten-2"
              size="80"
            >
              fa-check
            </v-icon>
            <p class="text_title_success">ส่งรายงานสำเร็จ</p>
            <v-card-text align="left"
              >รายงานดังกล่าวถูกส่งมอบให้เจ้าที่รับผิดชอบแล้ว
              ท่านสามารถติดตามสถานะการดำเนินงานได้ที่หน้าผู้ใช้งานหลัก</v-card-text
            >
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                rounded
                large
                text
                color="green darken-1"
                @click="confirmResult"
                >ยืนยัน</v-btn
              >
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>

        <!-- SnackBar handler update case to server status -->
        <v-snackbar v-model="snb_updateStatus" :timeout="snb_timeout">
          {{ message_UpdateStatus }}
          <v-btn :color="snb_color" text @click="snb_updateStatus = false">
            ตกลง
          </v-btn>
        </v-snackbar>

        <!-- Header of Content -->
        <v-row>
          <v-col cols="12">
            <div class="ml-9">
              <p class="header_1">รายงานโฆษณาเกินจริงหรือเข้าข่ายห้ามโฆษณา</p>
            </div>
          </v-col>
        </v-row>

        <!-- Content forms -->

        <v-row>
          <v-col cols="12" md="12">
            <div class="ml-5">
              <v-stepper class="elevation-0" v-model="compStep">
                <template>
                  <v-stepper-header class="elevation-0 mt-4">
                    <v-stepper-step :complete="compStep > 1" step="1">{{
                      stepName[0]
                    }}</v-stepper-step>
                    <v-divider></v-divider>
                    <v-stepper-step :complete="compStep > 2" step="2">{{
                      stepName[1]
                    }}</v-stepper-step>
                    <v-divider></v-divider>
                    <v-stepper-step :complete="compStep > 3" step="3">{{
                      stepName[2]
                    }}</v-stepper-step>
                    <v-divider></v-divider>
                    <v-stepper-step step="4">{{ stepName[3] }}</v-stepper-step>
                  </v-stepper-header>

                  <v-stepper-items>
                    <v-stepper-content
                      class="pa-0 ma-0"
                      v-for="n in steps"
                      :key="`${n}-content`"
                      :step="n"
                    >
                      <div v-if="n == 1">
                        <v-container class="fill-height fill-width">
                          <v-row class="text-left" justify="center">
                            <!-- Ads photos -->
                            <v-col cols="12" md="6" class="px-6">
                              <v-row>
                                <v-col align="left" md="12">
                                  <span class="mr-6 header_form"
                                    >รูปภาพแหล่งที่มาหรือใช้ในการโฆษณา</span
                                  >
                                  <v-col cols="12">
                                    <v-file-input
                                      outlined
                                      rounded
                                      v-model="s1_ads_photos"
                                      show-size
                                      counter
                                      multiple
                                      prepend-inner-icon="$file"
                                      prepend-icon=""
                                      accept="image/*"
                                      label="คลิกเพื่อเพิ่มรูปภาพ"
                                    >
                                      <template v-slot:selection="{ text }">
                                        <v-chip color="primary">
                                          {{ text }}
                                        </v-chip>
                                      </template>
                                    </v-file-input>
                                    <v-col class="ml-1">
                                      <p
                                        v-if="!imgStatus"
                                        class="text_status_imgFailed"
                                      >
                                        (สถานะ ยังไม่บันทึก)
                                      </p>
                                      <p v-else class="text_status_imgSuccess">
                                        (สถานะ บันทึกสำเร็จ)
                                      </p>

                                      <p class="text_warning_danger">
                                        ข้อควรระวัง: รูปภาพจะไม่ถูกบันทึก
                                        หากไม่กดปุ่มบันทึกรูปภาพ
                                      </p>

                                      <v-alert
                                        dismissible
                                        transition="scroll-x-transition"
                                        v-if="imgNotfound"
                                        type="error"
                                        class="mt-5"
                                      >
                                        กรุณาเลือกรูปภาพอย่างน้อยจำนวน 1 รูปภาพ
                                      </v-alert>
                                    </v-col>

                                    <v-col
                                      cols="12"
                                      class="mt-1"
                                      justify="center"
                                      align="right"
                                    >
                                      <v-btn
                                        depressed
                                        rounded
                                        large
                                        block
                                        color="primary"
                                        @click="uploadIMG"
                                        >บันทึกรูปภาพ</v-btn
                                      >
                                    </v-col>
                                  </v-col>

                                  <v-card
                                    outlined
                                    flat
                                    justify="center"
                                    align="center"
                                  >
                                    <div
                                      v-if="
                                        s1_photos_show.length == 0 ||
                                          !s1_photos_show
                                      "
                                      justify="center"
                                      align="center"
                                      class="pa-5"
                                    >
                                      <v-icon
                                        class="py-5"
                                        size="110"
                                        color="#CDCDCD"
                                        >fa-images</v-icon
                                      >
                                      <span class="subtitleB">
                                        <br />พื้นที่แสดงรูปภาพที่ใช้ในการโฆษณา
                                        <br />(รูปภาพจะปรากฎเมื่อทำการอัปโหลดสำเร็จ)
                                      </span>
                                      <span class="subtitle"
                                        ><br />การอัปโหลดรูปภาพมายัง Tawai for
                                        Health</span
                                      >
                                      <br />
                                      <span class="subtitle"
                                        >ข้าพเจ้ายอมรับ ข้อตกลง Terms of Use and
                                        Privacy Policy ของทาง TaWai</span
                                      >
                                    </div>
                                    <div v-else class="pa-10">
                                      <v-row>
                                        <v-col
                                          v-for="(n, index) in s1_photos_show"
                                          :key="index"
                                          class="d-flex child-flex"
                                          cols="6"
                                        >
                                          <v-img
                                            :src="n"
                                            contain
                                            aspect-ratio="1.3"
                                            class="grey lighten-2"
                                          >
                                          </v-img>
                                        </v-col>
                                      </v-row>
                                    </div>
                                  </v-card>
                                </v-col>
                              </v-row>
                            </v-col>

                            <!-- Ads Detailed -->
                            <v-col cols="12" md="6" align="left">
                              <p class="header_form">รายละเอียดการโฆษณา</p>
                              <v-form
                                ref="form"
                                v-model="valid_step1"
                                lazy-validation
                              >
                                <v-textarea
                                  label="ข้อความที่สงสัยว่าโฆษณาเกินจริง"
                                  auto-grow
                                  outlined
                                  v-model="s1_ads_detail"
                                  required
                                  :error-messages="adsErrors"
                                  @input="$v.s1_ads_detail.$touch()"
                                  @blur="$v.s1_ads_detail.$touch()"
                                ></v-textarea>
                              </v-form>
                            </v-col>
                          </v-row>

                          <v-row justify="center" align="center">
                            <v-btn
                              depressed
                              rounded
                              large
                              class="ma-4"
                              width="25%"
                              color="gray"
                              @click="exitReport()"
                              >ยกเลิก</v-btn
                            >
                            <v-btn
                              depressed
                              rounded
                              large
                              class="ma-4"
                              width="25%"
                              :disabled="!valid_step1"
                              color="primary"
                              @click="nextStep(n)"
                              >ถัดไป</v-btn
                            >
                          </v-row>
                        </v-container>
                      </div>

                      <div v-if="n == 2">
                        <v-container class="fill-height fill-width">
                          <v-row>
                            <v-col cols="12" align="left">
                              <v-row justify="start" class="mt-2">
                                <v-col cols="12" align="left">
                                  <p class="header_form">
                                    ประเภทผลิตภัณฑ์ที่โฆษณา
                                  </p>
                                </v-col>
                              </v-row>
                              <v-row justify="start">
                                <!-- Select ProductType -->
                                <v-col cols="12" md="6" align="left">
                                  <v-row>
                                    <v-form
                                      ref="form"
                                      v-model="valid_step2"
                                      lazy-validation
                                    >
                                      <v-select
                                        label="เลือกประเภทผลิตภัณฑ์"
                                        :items="list_productType"
                                        @change="getPDTypeINDEX"
                                        outlined
                                        rounded
                                        v-model="s2_pd_detail.pd_pdTypeName"
                                        required
                                        :error-messages="productTypeErrors"
                                        @input="
                                          $v.s2_pd_detail.pd_pdTypeName.$touch()
                                        "
                                        @blur="
                                          $v.s2_pd_detail.pd_pdTypeName.$touch()
                                        "
                                      ></v-select>
                                    </v-form>
                                  </v-row>
                                </v-col>
                                <!-- ProductType Description -->
                                <v-col cols="12" md="6" align="start">
                                  <div
                                    v-if="s2_pd_detail.pd_pdTypeName != ''"
                                    class="subtitle"
                                  >
                                    <v-row>
                                      <v-avatar
                                        slot="icon"
                                        color="#00B5F8"
                                        size="40"
                                        class="mx-2 mt-3"
                                      >
                                        <v-icon color="white">
                                          {{
                                            list_pdTypeIcon[
                                              s2_pd_detail.pd_pdTypeIndex - 1
                                            ]
                                          }}
                                        </v-icon>
                                      </v-avatar>

                                      <v-col cols="12" md="6" class="px-1 py-1">
                                        <span class="subtitle_bold">
                                          {{ s2_pd_detail.pd_pdTypeName }}
                                        </span>
                                        <span class="subtitle">
                                          คือ
                                          {{
                                            list_productDescription[
                                              s2_pd_detail.pd_pdTypeIndex - 1
                                            ]
                                          }}</span
                                        >
                                      </v-col>
                                    </v-row>
                                  </div>
                                  <span v-else class="subtitle">
                                    กรุณาระบุประเภทของผลิตภัณฑ์ที่พบ
                                  </span>
                                </v-col>
                              </v-row>

                              <v-divider></v-divider>

                              <v-row justify="start" class="mt-4">
                                <v-col cols="12" md="6" align="left">
                                  <p class="header_form">
                                    ชื่อผลิตภัณฑ์ที่ใช้ในการโฆษณา
                                  </p>
                                </v-col>
                              </v-row>

                              <!-- Only one name -->
                              <div
                                v-if="
                                  s2_pd_detail.pd_pdTypeIndex - 1 == 2 ||
                                    s2_pd_detail.pd_pdTypeIndex - 1 == 4
                                "
                              >
                                <v-row justify="start">
                                  <v-col cols="12" md="6" align="left">
                                    <v-form
                                      ref="form"
                                      v-model="valid_step2"
                                      lazy-validation
                                    >
                                      <v-text-field
                                        class="pt-2"
                                        label="ชื่อผลิตภัณฑ์"
                                        outlined
                                        rounded
                                        v-model="s2_pd_detail.pd_nameTH"
                                        :error-messages="productNameErrors"
                                      ></v-text-field>
                                    </v-form>
                                  </v-col>
                                </v-row>
                              </div>
                              <!-- Common and Formal Name -->
                              <div v-else>
                                <v-row justify="center">
                                  <v-col cols="12" md="6" align="left">
                                    <v-form
                                      ref="form"
                                      v-model="valid_step2"
                                      lazy-validation
                                    >
                                      <v-text-field
                                        class="pt-2"
                                        label="ชื่อสามัญ (ภาษาไทย)"
                                        outlined
                                        rounded
                                        v-model="s2_pd_detail.pd_cNameTH"
                                        :error-messages="productNameErrors"
                                      ></v-text-field>
                                      <v-text-field
                                        class="pt-2"
                                        label="ชื่อทางการค้า (ภาษาไทย)"
                                        outlined
                                        rounded
                                        v-model="s2_pd_detail.pd_fNameTH"
                                        :error-messages="productNameErrors"
                                      ></v-text-field>
                                    </v-form>
                                  </v-col>

                                  <v-col cols="12" md="6" align="left">
                                    <v-form
                                      ref="form"
                                      v-model="valid_step2"
                                      lazy-validation
                                    >
                                      <v-text-field
                                        class="pt-2"
                                        label="ชื่อสามัญ (ภาษาอังกฤษ)"
                                        outlined
                                        rounded
                                        v-model="s2_pd_detail.pd_cNameEN"
                                        :error-messages="productNameErrors"
                                      ></v-text-field>
                                      <v-text-field
                                        class="pt-2"
                                        label="ชื่อทางการค้า (ภาษาอังกฤษ)"
                                        outlined
                                        rounded
                                        v-model="s2_pd_detail.pd_fNameEN"
                                        :error-messages="productNameErrors"
                                      ></v-text-field>
                                    </v-form>
                                  </v-col>
                                </v-row>
                              </div>

                              <v-row justify="center">
                                <v-btn
                                  depressed
                                  rounded
                                  large
                                  class="ma-8"
                                  width="25%"
                                  color="gray"
                                  @click="backStep(n)"
                                  >ย้อนกลับ</v-btn
                                >
                                <v-btn
                                  depressed
                                  rounded
                                  large
                                  class="ma-8"
                                  width="25%"
                                  :disabled="!valid_step2"
                                  color="primary"
                                  @click="nextStep(n)"
                                  >ถัดไป</v-btn
                                >
                              </v-row>
                            </v-col>
                          </v-row>
                        </v-container>
                      </div>

                      <div v-if="n == 3">
                        <ProductSource
                          :s3_src_detail="s3_src_detail"
                          :src_photo_show="s3_photos_show"
                        />
                      </div>

                      <div v-if="n == 4">
                        <ReportFakeAds
                          :s1_ads_detail="s1_ads_detail"
                          :s2_pd_detail="s2_pd_detail"
                          :s3_src_detail="s3_src_detail"
                          :s1_ads_photoShow="s1_photos_show"
                          :s3_src_photoShow="s3_photos_show"
                        />

                        <v-row justify="center">
                          <v-btn
                            depressed
                            rounded
                            large
                            class="ma-8"
                            width="25%"
                            color="gray"
                            @click="backStep(n)"
                            >ย้อนกลับ</v-btn
                          >
                          <v-btn
                            depressed
                            rounded
                            large
                            class="ma-8"
                            width="25%"
                            color="primary"
                            @click="nextStep(n)"
                            >ส่งรายงาน</v-btn
                          >
                        </v-row>
                      </div>
                    </v-stepper-content>
                  </v-stepper-items>
                </template>
              </v-stepper>
            </div>
          </v-col>
        </v-row>
      </div>
    </v-row>
  </v-container>
</template>

<script>
import ProductSource from "./form/ProductSource";
import ReportFakeAds from "./display/ReportFakeAds";
import EventBus from "./EventBus";
import { validationMixin } from "vuelidate";
import { required } from "vuelidate/lib/validators";

export default {
  components: {
    ProductSource,
    ReportFakeAds
  },
  mixins: [validationMixin],
  validations: {
    s1_ads_detail: { required },
    s2_pd_detail: {
      pd_pdTypeName: { required }
    }
  },
  data: () => ({
    userProperties: {},
    caseProperties: {},
    valid_step1: false,
    valid_step2: false,
    dataMissingModal: false,
    caseSendingModal: false,
    caseResultModal: false,
    imgNotfound: false,
    imgStatus: false,
    snb_updateStatus: false,
    message_UpdateStatus: "",
    snb_color: "",
    snb_timeout: 2000,
    compStep: 1,
    steps: 4,
    editMODE: true,
    loadingCaseSending: false,
    list_productType: [],
    list_productDescription: [],
    stepName: ["ข้อมูลโฆษณา", "ข้อมูลผลิตภัณฑ์", "จุดเผยแพร่", "สรุปรายงาน"],
    list_pdTypeIcon: [
      "fa-pills",
      "fa-capsules",
      "fa-seedling",
      "fa-tablets",
      "fa-prescription-bottle",
      "fa-stethoscope",
      "fa-magic",
      "fa-utensils",
      "fa-vial",
      "fa-skull"
    ],
    s1_ads_photos: [],
    s1_ads_detail: "",
    s2_pd_detail: {
      pd_pdTypeName: "",
      pd_pdTypeIndex: 1,
      pd_nameTH: "",
      pd_cNameTH: "",
      pd_cNameEN: "",
      pd_fNameTH: "",
      pd_fNameEN: ""
    },
    s3_src_detail: {
      src_srcTypeName: "",
      src_srcTypeIndex: 0, //ChannelID
      src_subSrcTypeName: "", //subName
      src_subSrcTypeIndex: 0, //ChannelSubID
      src_subInfoName: "", //Name
      src_dateOnAir: "", //date
      src_timeOnAir: "", //Period
      src_adrs: "",
      src_prov: "",
      src_dist: "",
      src_subDist: "",
      src_pName: "",
      src_pTel: "",
      src_pContact: "",
      src_pLicense: "",
      src_pCar: "",
      src_pCarColor: ""
    },
    s1_photos_show: [],
    s3_photos_show: []
  }),
  computed: {
    adsErrors() {
      const errors = [];
      if (!this.$v.s1_ads_detail.$dirty) return errors;
      !this.$v.s1_ads_detail.required &&
        errors.push("กรุณาระบุรายละเอียดข้อความที่ใช้ในการโฆษณา");
      return errors;
    },
    productTypeErrors() {
      const errors = [];
      if (!this.$v.s2_pd_detail.pd_pdTypeName.$dirty) return errors;
      !this.$v.s2_pd_detail.pd_pdTypeName.required &&
        errors.push("กรุณาระบุประเภทของผลิตภัณฑ์");
      return errors;
    },
    productNameErrors() {
      const errors = [];
      if (
        this.s2_pd_detail.pd_nameTH.length < 1 &&
        this.s2_pd_detail.pd_cNameTH.length < 1 &&
        this.s2_pd_detail.pd_cNameEN.length < 1 &&
        this.s2_pd_detail.pd_fNameTH.length < 1 &&
        this.s2_pd_detail.pd_fNameEN.length < 1
      ) {
        errors.push("กรุณาระบุชื่อผลิตภัณฑ์อย่างน้อย 1 ชื่อ");
      }
      return errors;
    }
  },
  watch: {
    compStep() {
      // console.log("watcherWorking...");
      this.updateToServ();
    }
  },
  methods: {
    async checkUserAuth_local() {
      let checkAuth = localStorage.getItem("userToken");
      if (checkAuth) {
        let userID = localStorage.getItem("userID");
        await fetch(
          "http://www.tawainetwork.org/webBackendPHP/recheckSession.php",
          {
            method: "POST",
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json"
            },
            body: JSON.stringify({
              userID: userID,
              userToken: checkAuth
            })
          }
        )
          .then(res => {
            return res.json();
          })
          .then(dataRes => {
            // console.log(' - DATA: ', dataRes)
            if (dataRes[0]["status"] == "failed") {
              this.$router.push("login");
              localStorage.removeItem("userToken");
              localStorage.removeItem("userID");
              localStorage.removeItem("userRole");
            } else {
              //wait for edit function and loadData from data to variable
              this.fetchProductType();
            }
          })
          .catch(err => {
            console.log(
              "ERR.requestAccess.Session_createReport.FakeAds: ",
              err
            );
          });
      } else {
        this.$router.push("/");
      }
    },
    async fetchProductType() {
      await fetch(
        "http://www.tawainetwork.org/webBackendPHP/fetchChoiceList.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            requestType: "productType"
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log(' - DATA: ', dataRes)
          if (dataRes[0]["status"] == "success") {
            this.list_productType = JSON.parse(
              JSON.stringify(dataRes[1].map(label => label.ProductTypeName))
            );
            this.list_productDescription = JSON.parse(
              JSON.stringify(dataRes[1].map(label => label.Description))
            );
          }
        })
        .catch(err => {
          console.log("ERR.requestAccess.fetchList.productType: ", err);
        });
    },
    async getComp_s3() {
      await EventBus.$on("case_fakeAds_comp", dataBUS => {
        this.compStep = dataBUS;
      });
      await EventBus.$on("s3_src_detail", dataBUS => {
        // console.log('srcDetailed ', dataBUS);
        this.s3_src_detail = dataBUS;
      });
      await EventBus.$on("s3_photos", dataBUS => {
        this.s3_photos_show = dataBUS;
      });
    },
    async uploadIMG() {
      if (this.s1_ads_photos.length > 0) {
        this.imgNotfound = false;
        var formData = new FormData();
        // console.log('prepare to caseProperties ', this.caseProperties);
        formData.append("reqType", "ads");
        formData.append("caseID", this.caseProperties.caseID);
        formData.append("userID", this.userProperties.userID);
        for (let index = 0; index < this.s1_ads_photos.length; index++) {
          formData.append("image[]", this.s1_ads_photos[index]);
        }

        await fetch(
          "http://www.tawainetwork.org/webBackendPHP/uploadImages.php",
          {
            method: "POST",
            headers: {
              Accept: "application/json"
            },
            body: formData
          }
        )
          .then(res => {
            return res.json();
          })
          .then(dataRes => {
            // console.log(' - DATA: ', dataRes, ' TTTT: ', dataRes[dataRes.length-1])
            let stillAlive = false;
            for (let index = 0; index < dataRes.length - 1; index++) {
              if (dataRes[index]["status"] != "success") {
                stillAlive = false;
                break;
              } else {
                stillAlive = true;
              }
            }
            if (stillAlive) {
              let splitImgPath = dataRes[dataRes.length - 1].split(",");
              splitImgPath.pop();
              this.s1_photos_show = splitImgPath;
              this.imgStatus = true;
            } else {
              this.imgStatus = false;
            }
          })
          .catch(err => {
            console.log("ERR.upload.img.ads: ", err);
          });
      } else {
        this.imgNotfound = true;
      }
    },
    async queryFakeAds() {
      if (this.caseProperties.manageMode == "edit") {
        // console.log("req.editReport #", this.caseProperties.caseID);
        await fetch(
          "http://www.tawainetwork.org/webBackendPHP/getCaseDetailed.php",
          {
            method: "POST",
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json"
            },
            body: JSON.stringify({
              userID: this.userProperties.userID,
              caseID: this.caseProperties.caseID,
              caseTypeID: "3"
            })
          }
        )
          .then(res => {
            return res.json();
          })
          .then(dataRes => {
            // console.log(' - DATA: ', dataRes)
            if (dataRes[0]["status"] == "success") {
              this.s1_ads_detail = dataRes[1]["FakeAdsDetail"];
              this.s2_pd_detail = {
                pd_pdTypeName: dataRes[1]["ProductTypeName"],
                pd_pdTypeIndex: dataRes[1]["ProductTypeId"],
                pd_nameTH: dataRes[1]["ProductName"],
                pd_cNameTH: dataRes[1]["CommonNameTh"],
                pd_cNameEN: dataRes[1]["CommonNameEn"],
                pd_fNameTH: dataRes[1]["FormalNameTh"],
                pd_fNameEN: dataRes[1]["FormalNameEn"]
              };
              let temp_extractDate = dataRes[1]["date"]["date"];
              temp_extractDate = temp_extractDate.substr(0, 10);
              var subSrcName = "";
              if (dataRes[1]["SubChannelName"]) {
                subSrcName = dataRes[1]["SubChannelName"];
              } else {
                subSrcName = dataRes[1]["subName"];
              }

              this.s3_src_detail = {
                src_srcTypeName: dataRes[1]["ChannelName"],
                src_srcTypeIndex: dataRes[1]["ChannelId"],
                src_subSrcTypeName: subSrcName,
                src_subSrcTypeIndex: dataRes[1]["ChannelSubId"],
                src_subInfoName: dataRes[1]["Name"],
                src_dateOnAir: temp_extractDate,
                src_timeOnAir: dataRes[1]["Period"],
                src_adrs: dataRes[1]["Adr"],
                src_prov: dataRes[1]["Province"],
                src_dist: dataRes[1]["District"],
                src_subDist: dataRes[1]["SubDistrict"],
                src_pName: dataRes[1]["details"],
                src_pTel: dataRes[1]["TelNo"],
                src_pContact: dataRes[1]["ContactMethod"],
                src_pLicense: dataRes[1]["LicenseNo"],
                src_pCar: dataRes[1]["CarBrand"],
                src_pCarColor: dataRes[1]["CarColor"]
              };
              if (dataRes[1]["adsPhotos"]) {
                let splitImgPath = dataRes[1]["adsPhotos"].split(",");
                if (splitImgPath.length > 1) {
                  splitImgPath.pop();
                }
                this.s1_photos_show = splitImgPath;
              } else {
                this.s1_photos_show = [];
              }
              if (dataRes[1]["srcPhotos"]) {
                let splitImgPath = dataRes[1]["srcPhotos"].split(",");
                if (splitImgPath.length > 1) {
                  splitImgPath.pop();
                }
                this.s3_photos_show = splitImgPath;
              } else {
                this.s3_photos_show = [];
              }
            }
          })
          .catch(err => {
            console.log("ERR.request.getCase.fakeAds: ", err);
          });
      }
    },
    async updateToServ() {
      await fetch(
        "http://www.tawainetwork.org/webBackendPHP/postUpdate_caseAds.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            userID: this.userProperties.userID,
            caseID: this.caseProperties.caseID,
            editMODE: this.editMODE,
            s1_ads_detail: this.s1_ads_detail,
            s2_pd_detail: this.s2_pd_detail,
            s3_src_detail: this.s3_src_detail
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log(' - DATA: ', dataRes);
          if (dataRes[0]["status"] == "failed") {
            this.snb_updateStatus = true;
            this.message_UpdateStatus =
              "เกิดข้อผิดพลาดในการอัปเดตข้อมูล กรุณาลองใหม่อีกครั้ง";
            this.snb_color = "pink";
          } else {
            if (this.loadingCaseSending && !this.editMODE) {
              this.loadingCaseSending = false;
              this.caseSendingModal = false;
              this.caseResultModal = true;
            } else {
              this.snb_updateStatus = true;
              this.message_UpdateStatus = "อัปเดตข้อมูลสำเร็จ";
              this.snb_color = "green";
            }
          }
        })
        .catch(err => {
          console.log("ERR.request.getCase.fakeAds: ", err);
        });
    },
    getPDTypeINDEX() {
      let pdTypeINDEX = this.list_productType.findIndex(
        index => index === this.s2_pd_detail.pd_pdTypeName
      );
      this.s2_pd_detail.pd_pdTypeIndex = pdTypeINDEX += 1;
    },
    nextStep(n) {
      if (n === this.steps) {
        this.caseSendingModal = true;
      } else if (n == 1) {
        this.$v.$touch();
        if (this.valid_step1 && this.s1_ads_detail.length > 0) {
          this.compStep = n + 1;
        }
      } else if (n == 2) {
        this.$v.$touch();
        if (
          this.valid_step2 &&
          this.s2_pd_detail.pd_pdTypeName.length > 0 &&
          (this.s2_pd_detail.pd_nameTH.length > 0 ||
            this.s2_pd_detail.pd_cNameTH.length > 0 ||
            this.s2_pd_detail.pd_cNameEN.length > 0 ||
            this.s2_pd_detail.pd_fNameTH.length > 0 ||
            this.s2_pd_detail.pd_fNameEN.length > 0)
        ) {
          this.compStep = n + 1;
        }
      } else {
        this.compStep = n;
      }
    },
    backStep(n) {
      if (n === 1) {
        this.compStep = 1;
      } else {
        this.compStep = n - 1;
      }
    },
    exitReport() {
      this.dataMissingModal = false;
      this.$router.push("/mainuser");
    },
    sendingCase() {
      this.editMODE = false;
      this.loadingCaseSending = true;
      this.updateToServ();
    },
    confirmResult() {
      this.caseResultModal = false;
      this.$router.push("/mainuser");
    }
  },
  created() {
    this.userProperties = this.$store.getters.getUserProperties;
    this.caseProperties = this.$store.getters.getCaseProperties;
    if (this.userProperties.userID.length < 1) {
      this.dataMissingModal = true;
    }
  },
  mounted() {
    this.checkUserAuth_local();
    this.getComp_s3();
    this.queryFakeAds();
  }
};
</script>

